.row {
  // max-width: 114rem; // the size when having enough space, 100% when not enough

  &:not(:last-child) { // everything except the last child
    margin-bottom: $gutter-vertical;
  }

  @include clearfix;

  [class^="col-"] {
    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }

  .col-1-of-2 {
    width: 50rem;
  }

  .col-1-of-3 {
    width: calc((100% - #{$gutter-horizontal} * 2) / 3);
  }

  .col-1-of-4 {
    width: calc((100% - #{$gutter-horizontal} * 3) / 4);
  }

  .col-1-of-5 {
    width: calc((100% - #{$gutter-horizontal} * 4) / 5);
  }

  .col-4-of-5 {
    width: calc((100% - #{$gutter-horizontal}) * 4 / 5 + #{$gutter-horizontal} / 2);
  }

  .col-2-of-4 {
    width: calc((100% - #{$gutter-horizontal}) / 2);
  }

  .col-3-of-4 {
    width: calc((100% - #{$gutter-horizontal}) * 3 / 4 + #{$gutter-horizontal} / 2);
  }
}